export default {
	data() {
		return {
			userIsLogin: false,
			userInfo: {}
		}
	},
	
	onShow() {
		// 使用挂载方法获取用户数据
		var userInfo = this.getGlobalUser("userInfo");
		// debugger;
		if (userInfo != null) {
			this.userIsLogin = true;
			this.userInfo = userInfo;
		} else {
			this.userIsLogin = false;
			this.userInfo = {};
			// 切换页面跳转，使用tab切换的api
			uni.navigateTo({
				url: "../login/login"
			});
		}
	},
	
	onShow() {
		// 使用挂载方法获取用户数据
		var userInfo = this.getGlobalUser("userInfo");
		// debugger;
		if (userInfo != null) {
			this.userIsLogin = true;
			this.userInfo = userInfo;
		} else {
			this.userIsLogin = false;
			this.userInfo = {};
			// 切换页面跳转，使用tab切换的api
			uni.navigateTo({
				url: "../login/login"
			});
		}
	},
	
	onLoad() {

	},
	methods: {

		changePassword() {
			uni.navigateTo({
				url: "../changePassword/changePassword"
			})				
		},
		
		timerSet() {
			uni.navigateTo({
				url: "../timerSet/timerSet"
			})		
		},
		
		cleanStorage() {
			uni.clearStorageSync();
			uni.showToast({
				title: "清理缓存成功",
				mask: false,
				duration: 1500
			})
		},
		
		logout() {
			var userInfo = this.getGlobalUser("userInfo");
			var serverUrl = this.serverUrl;
			uni.request({
				url: serverUrl + '/users/logout?id=' + userInfo.user_id,
				method: "POST",
				header:{
					'content-type': 'application/x-www-form-urlencoded',
					'id':userInfo.user_id,
					'token': userInfo.token
				},
				complete: (res) => {
					// 获取真实数据之前，务必判断状态是否为0
					// debugger;
					// if (res.data.code == 0) {
						// 如果服务器返回0，代表用户在服务端退出登录成功	
						uni.removeStorageSync("userInfo");							
						uni.navigateTo({
							url: "../login/login"
						})
					// } else {
					// 	
					// }
				}
			});
		}

	}
}
